async function BuyGifts(buyReq) {
  const raw = JSON.stringify({
    gift_id: buyReq.giftID,
    payer: {
      email: buyReq.email,
      first_name: buyReq.name,
      last_name: buyReq.lastName,
      cpf: buyReq.CPF
    },
    message: buyReq.message
  });

  const requestOptions = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: raw
  };
  const response = await fetch(`${process.env.REACT_APP_API_URL}/buy`, requestOptions);
  const data = await response.json();
  return data;
}

async function CheckPaymentStatus(paymentID) {
  const requestOptions = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({
      "id": `${paymentID}`
    })
  };
  const response = await fetch(`${process.env.REACT_APP_API_URL}/paymentStatus`, requestOptions);
  const data = await response.json();
  return data;
}

export { BuyGifts, CheckPaymentStatus };
